<template>
  <div class="wrap-nav data-wrap">
      <Header :htitle = "msg" :changew="msgb" :icoS="leftIco" :icob="rightIco" :icobS="icoRS" :icoSS="icoLS"></Header>
      <div class="contain">
        <div class="top">
          <div class="yearSelect">年份选择
            <select v-model="yearVal">
              <option v-for="(item,index) in dataList.yearList" v-bind:key="index" :value="item">{{ item }}</option>
            </select>
          </div>
          <div class="monthSelect">月度选择
            <select v-model="monthVal">
              <option v-for="(item,index) in dataList.monthList" v-bind:key="index" :value="item">{{ item }}</option>
            </select>
          </div>
          <div class="querybtn">
            <button @click="checkDate">查看</button>
          </div>
          <!-- <div class="switchtag">
            <span class="on">日数据</span>
            <span>月数据</span>
            <b></b>
          </div> -->
        </div>
        <div class="con">
          <div class="cell">
              <p class="h-4"><img src="../assets/hico.png">数据质量统计</p>
              <table v-if="overview">
                <tr>
                  <th style="width: 60%;">企业名称</th>
                  <th style="width: 15%;">监测点数</th>
                  <th style="width: 15%;">异常点数</th>
                  <th style="width: 10%;">占比</th>
                </tr>
                <tr v-for="(item,index) in dataChList.enterpriseList" v-bind:key="index" v-if="index < 5">
                  <td v-bind:style="{fontSize: (item.name.length > 17?0.26:0.32) + 'rem'}">{{ item.name }}</td>
                  <td>{{ item.monitoringPoints }}</td>
                  <td>{{ item.abnormalPoints }}</td>
                  <td>{{ item.Proportion }}%</td>
                </tr>
              </table>
          </div>
          <div class="cell datapic">
              <p class="h-4"><img src="../assets/hico.png">数据质量图</p>
              <div class="charttop">
                <span style="background:#fb7c5b"></span>异常
                <span style="background:#f8e177"></span>丢失
                <span style="background:#2dded4"></span>正常
              </div>
              <highcharts-container :defOptions="optionsa" style="width: 100%;height: 5.333333rem;"></highcharts-container>
          </div>
          <div class="cell">
              <p class="h-4"><img src="../assets/hico.png">数据质量概览</p>
              <div class="rowcell">
                <div class="cellj">
                  <p class="h-5"><img src="../assets/dq/h-51.png"></p>
                  <div class="metercell">
                    <span style="transform:rotate(20deg)"></span>
                    <p>{{ dataChList.meter }}</p>
                  </div>
                </div>
                <div class="cellj">
                  <p class="h-5"><img src="../assets/dq/h-52.png"></p>
                  <div class="rowcellj">
                      <highcharts-container :defOptions="optionsc" style="width: 50%;height: 2.466667rem;"></highcharts-container>
                      <div>
                        <p>数据异常：<span>{{ dataChList.dataUn }}</span></p>
                        <p>数据丢失：<span>{{ dataChList.dataLose }}</span></p>
                      </div>
                  </div>
                </div>
              </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
  import Header from '@/components/part/Header'
  import HighchartsContainer from '../components/part/chart'
  import data from '../../static/data/dataFirst'

  export default {
    name: 'dataQulity',
    data() {
      return {
        //顶部标题
        msg: '数据质量总览', //标题
        msgb: '填报', //右侧显示文字
        icoLS: true, //左边图标是否
        leftIco: 'backI', //closeI 关闭
        icoRS: true, //左边图标是否
        rightIco: 'wordI', // plusI 加号
        yearVal: 0, // 年份
        monthVal: 0, // 月份
        //dataIndex: 0, //数据索引
        dataList: {}, //数据
        overview: true, //总览
        fillin: false, //填报
        dataChList: {}, //可变数据
        //数据质量图
        optionsa: {},
        chartListA: [], //异常
        chartListB: [], //丢失
        chartListC: [], //正常
        //异常分布
        optionsc: {},
        dataUn: 0, //异常
        dataLose: 0, //丢失
        fontsizeA: 0.23,




      }
    },
    mounted() {
      this.getData(); //获取默认数据
    },
    methods: {
      //更新月份数据
      checkDate() {
        //console.log(this.yearVal+this.monthVal)
        let _this = this;
        var dateNum = _this.yearVal+_this.monthVal;
        var dataIndex = 0;
        _this.dataList.dqOverview.map(function(item,index){
          if(item.date == dateNum) {
            dataIndex = index;
            //console.log(dataIndex)
          }
        })
        _this.dataChList = _this.dataList.dqOverview[dataIndex];
        _this.chartListA = _this.dataChList.dataQ.unusual.map(function(item){
          return +item
        });
        _this.chartListB = _this.dataChList.dataQ.lose.map(function(item){
          return +item
        });
        _this.chartListC = _this.dataChList.dataQ.normal.map(function(item){
          return +item
        });
        _this.dataUn = parseInt(_this.dataChList.dataUn);
        _this.dataLose = parseInt(_this.dataChList.dataLose)
        _this.$set(_this.dataChList,_this.dataChList);
        console.log(this.dataChList)
      },

      //获取数据
      getData() {
        let _this = this;
        _this.dataList = data.res;
        //默认月份数据
        _this.dataChList = _this.dataList.dqOverview[0];
        //当月 new Date()是现在的时间 年月日时分秒 时区
        _this.yearVal = new Date().getFullYear();
        _this.monthVal = new Date().getMonth();
        //图表
        _this.chartListA = _this.dataChList.dataQ.unusual.map(function(item){
          return +item
        });
        _this.chartListB = _this.dataChList.dataQ.lose.map(function(item){
          return +item
        });
        _this.chartListC = _this.dataChList.dataQ.normal.map(function(item){
          return +item
        });
        _this.dataUn = parseInt(_this.dataChList.dataUn);
        _this.dataLose = parseInt(_this.dataChList.dataLose)
        _this.optionsa = {
          chart: { //基本信息
              type: 'column',
          backgroundColor: '#062f43',
          style: {
            color: '#ffffff'
          }
            },
        title: {
          text: null
        },
        legend: { //图例
          enabled: false
        },
        xAxis: { //X轴
          categories: ['7月','8月','9月','10月','11月','12月'],
          lineColor: '#09435f', //轴线颜色
          tickPixelInterval: 5,
              //crosshair: true,
          labels: {
            style: {
              color: '#e1e1e1',
              fontSize: ".266667rem"
            }
          }
        },
        yAxis: {
          min: 0,
          max: 1000,
          gridLineColor: '#09435f', //网格线颜色
          tickAmount: 6,
          tickPixelInterval: 35,
            title: {
            text: null
          },
          labels: {
            style: {
              color: '#e1e1e1',
              fontSize: ".266667rem"
            }
          },
          stackLabels: {  // 堆叠数据标签
            enabled: false,
          }
        },
        tooltip: {
          // head + 每个 point + footer 拼接成完整的 table
          headerFormat: '<span style="font-size:10px;color:#333;">{point.key}</span><table>',
          pointFormat: '<tr><td style="color:#333;padding:0">{series.name}: </td>' +
          '<td style="padding:0;color:#333;">{point.y:.1f}</td></tr>',
          footerFormat: '</table>',
          shared: true,
          useHTML: true
        },
        plotOptions: {
          column: {
            stacking: 'normal',
            dataLabels: { // 数据列上是否显示数值
              enabled: false
            },
          },
          series: {
            borderColor: 'none'
          }
        },
        series: [
          {name: '', color: '#fb7c5b', data: _this.chartListA},
          {name: '', color: '#f8e177', data: _this.chartListB},
          {name: '', color: '#2dded4', data: _this.chartListC}
        ],
        credits: { //版权信息
          enabled: false
        },
        exporting: { //导出按钮
          enabled: false
        }
        }
        this.optionsc = {
          chart: { //基本信息
            type: 'pie',
            backgroundColor: 'none',
            plotBackgroundColor: null,
            plotBorderWidth: null,
            plotShadow: false,
          },
          title: {
            text: null
          },
          legend: { //图例
            enabled: false
            },
            tooltip: {
              enabled: false
            },
            plotOptions: {
              pie: {
              allowPointSelect: true,
              cursor: 'pointer',
              dataLabels: {
                enabled: false
              },
              colors: ['#f8e177', '#fb7c5b']
            },
            series: {
              borderColor: 'none'
            }
            },
            series: [{ //数据
              name: '',
            data: [{
              name: '',
              y: _this.dataLose,
              sliced: true,
              selected: true
            }, {
              name: '',
              y: _this.dataUn
            }]
          }],
          credits: { //版权信息
            enabled: false
          },
          exporting: { //导出按钮
            enabled: false
          }
        }
      }
    },
    components: {
      Header,
      HighchartsContainer
    }
  }
</script>

<style lang="less" scoped>
  .data-wrap {
    .top {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 .333333rem;
      margin-bottom: .333333rem;
      div {
        font-size: .373333rem;
      }
      .yearSelect {
        select {
          width: 1.6rem;
          padding-left: .2rem;
          height: .6rem;
          background: #2dded4;
          border-radius: 50px;
        }
      }
      .monthSelect {
        select {
          width: 1.066667rem;
          padding-left: .2rem;
          height: .6rem;
          background: #2dded4;
          border-radius: 50px;
        }
      }
      .querybtn {
        button {
          background: #2dded4;
          color: #fff;
          border-radius: 4px;
          width: 1.333333rem;
          height: .6rem;
        }
      }
    }
    .contain {
      .cell {
        background: #062f43;
        margin-bottom: .2rem;
        padding: 0 .333333rem .2rem .333333rem;
      }
      table {
        width: 100%;
        tr {
          height: .533333rem;
          line-height: .533333rem;
        }
        th {
          background: #2dded4;
          font-size: .32rem;
          text-align: center;
        }
        td {
          background: #13435b;
          font-size: .32rem;
          text-align: center;
        }
      }
      .datapic {
        .charttop {
          display: flex;
          align-items: center;
          justify-content: flex-end;
          font-size: .266667rem;
          span {
            display: block;
            width: .2rem;
            height: .053333rem;
            margin: 0 .133333rem 0 .253333rem;
          }
        }
      }
      .rowcell {
        display: flex;
        margin-top: .4rem;
        .cellj {
          width: 50%;
        }
        .h-5 {
          margin-bottom: .2rem;
          text-align: center;
          img {
            width: 2.506667rem;
          }
        }
        .metercell {
          width: 1.733333rem;
          height: 1.733333rem;
          background: url(../assets/dq/meter.png) no-repeat;
          background-size: contain;
          margin: auto;
          display: flex;
          align-items: flex-end;
          justify-content: center;
          position: relative;
          span {
            width: .066667rem;
            height: .506667rem;
            background: url(../assets/dq/pointer.png) no-repeat;
            background-size: contain;
            position: absolute;
            left: 50%;
            top: .513333rem;
            transform-origin: .033333rem .253333rem;
          }
          p {
            font-family: digital;
            font-size: .266667rem;
          }
        }
        .rowcellj {
          display: flex;
          width: 100%;
          align-items: center;
          p {
            font-size: .266667rem;
            span {
              font-size: .266667rem;
              font-family: digital;
              color: #f8e177;
            }
          }
        }
      }
    }
    .highcharts-point {
      width: .2rem;
      height: .04rem;
      border-radius: 0;
    }
  }
</style>



